//引入支付密码的组件
import zhifupassword from "@/components/yzc-paykeyboard/yzc-paykeyboard.vue"
const app = getApp()
export default {
	data(){
		return {
			show:false,
			isIphoneX:false,
			zhifuppastext:""
		}
	},
	methods:{
		Confirmusergoods(){
			this.show=true
			this.isIphoneX = true
		},
		// 点击支付密码那个×
		close(e){
			this.show = false,
			this.isIphoneX = false
		},
		// 等输入密码完成后
		Enterpasswordcompletepayment(e){
			const _this = this
			_this.zhifuppastext = e
			console.log(e)
			//当输入完密码时
			uni.request({
				url:`${app.globalData.Requestpath}order/confirmPayOrder`,
				method:"POST",
				data:{
					token:_this.tokey,
					o_sn:_this.ordersnSerialid,
					pay_password:_this.zhifuppastext
				},
				success(res) {
					console.log(res)
					if(res.data.code==0){
						_this.show = false,
						_this.isIphoneX = false
						app.globalData.showtoastsame('收货完成')
						//就跳到评价的页面
						return 
					}
					app.globalData.showtoastsame(res.data.msg)
				}
			})
		}
	},
	props:["tokey","ordersnSerialid"],
	components:{
		zhifupassword
	}
}
